const app = getApp();
import helper from '../common/helper.js';
import uniPopup from '@/components/uni-popup.vue';
export default {
	data() {
		return {
			tableData: [{ name: '大锤', age: '17777777777', address: '2019-10-25' }, { name: '张三', age: '21', address: '成都' }, { name: '李四', age: '16', address: '南京' }],
			columns: [{ title: '用户名', key: 'name' }, { title: '手机号', key: 'age' }, { title: '时间', key: 'address' }],
			bottomData: [
				{
					text: '微信好友',
					icon: 'iconweixin'
				},
				{
					text: '微信朋友圈',
					icon: 'iconpengyouquan'
				}
			],
			collectionList: {},
			imgUrl: '',
			isShow: true,
		};
	},
	components: {
		// wTable,
		uniPopup
	},
	onLoad() {
		this.content();
		this.imgUrl = helper.imgUrl;
	},
	methods: {
		goMore(){
			this.isShow = false
		},
		quickInlet(e) {
			if (e == 1) {
				this.$refs.showshare.open();
			} else if (e == 2) {
				this.$refs.center.open();
			}
			// console.log(e);
		},
		cancel(type) {
			this.$refs['show' + type].close();
		},
		content() {
			uni.request({
				url: `${helper.requestUrl}/promote-rebates`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					// console.log(res);
					if (res.data.status_code == 200) {
						this.collectionList = res.data;
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		goShare(e) {
			// console.log(e)
			let sceneType = ''
			if(e == '微信好友'){
				sceneType = 'WXSceneSession'
			} else if(e == '微信好友'){
				sceneType = 'WXSenceTimeline'
			}
			uni.share({
				provider: 'weixin',
				scene: sceneType,
				type: 1,
				summary: this.collectionList.share_link,
				success: function(res) {
					// console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					// console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	}
};
